import React from 'react';
//import Item from "./Item";

function Item(props){
    return (    
        <div  style = {props.cell} onClick = {() => {props.hendlerAddCell(props.key_i, props.key_j)}}></div>
        )
    } 


export default function ItemsField(props){
    
    let ItemList = () => {
            let tempArray = [];
            for(let i = 0; i < props.fieldheight; i++){
                tempArray[i] = [];
                for(let j = 0; j < props.fieldwidth; j++){

                    tempArray[i][j] = <Item  
                        cell = {props.field[i][j]} 
                        key={i.toString() + j.toString()}
                        hendlerAddCell = {(el1, el2) => {props.hendlerAddCell(el1,el2)}}
                        key_i = {i}
                        key_j = {j}
                    />
                }
            }
            return tempArray
        }

       

    return (
       
        <div style = {props.sizeAppField}>   
            {console.log('ItemList')}
            {ItemList()}
        </div>
    )
  
}


