import React, {useState}  from 'react';

export default function InputForm(props){

    let [time, setTime] = useState(100);

        return(         
        
            <h1 className ='gameoflife__App__h1'>Interval
                <input className ='gameoflife__App__input' type="number"  value = {time} onChange = {(e) => {props.ChangeTime(e); setTime(e.target.value)}}></input> 
            </h1>  
    
                 
        )
    
}