import React, { useState, useEffect } from 'react';
import {NavLink} from 'react-router-dom';

function ElementofLink(props) {
  return <span style = { props.ColorLetter}>{props.letter}
  </span>
}

function CreateWordLink(props) {
  return(
    props.link.split('').map((lett, index) => <ElementofLink 
    letter = {lett} ColorLetter = {props.ArrColor[index]} >
    </ElementofLink>
    )
  )
}

function CreateLink(props) {
  return(
    props.arrPages.map(lk => {
      return(
        <div className = {`Navbar__${lk.link}`} >
              <NavLink to = { `/${lk.link}`} 
              key = {`${lk.link}`}
                  className = 'pd10'
                  onMouseEnter = {(e) => props.hendlerColorOverOut(lk.link, lk.col, lk.setCol, true, e) }
                  onMouseLeave =   {(e) =>  props.hendlerColorOverOut(lk.link, lk.col, lk.setCol, false, e)} >
                      <CreateWordLink link = {lk.link} ArrColor = {lk.col}></CreateWordLink>
              </NavLink>
             
      </div>
      
      )
    })
  )
}


export default function Navbar() {


  
  let [colHome, setColHome] = useState([]);
  let [colPortfolio, setColPortfolio] = useState([]);
  let [colResume, setColResume] = useState([]);
  let [colContacts, setColContacts] = useState([]);
 
  let arrPages = [
    {link: 'Home', col: colHome, setCol: setColHome},
    {link:'Portfolio', col: colPortfolio, setCol: setColPortfolio},
    {link:'Resume', col: colResume, setCol: setColResume},
    {link:'Contacts', col: colContacts, setCol: setColContacts}];


    useEffect(() => {
      arrPages.map((elemPage, indexPage) => {
       return  elemPage.setCol(
          elemPage.link.split('').map((el, indx) => elemPage.col[indx] = {color:'grey'})
        )
      })
    }, [])

      function hendlerColorOverOut(link, color, setColor, event, e){
        let animationTimer = 0;
        let setAnimation = new Set();
                function OneTimeOutOfAnimation() {
                  setColor(
                      color.map(
                        (itemColorLetter, index) => {
                          setAnimation.add(Math.floor(link.length/2) + animationTimer);
                          setAnimation.add(0);
                          setAnimation.add(2);
                            setAnimation.forEach(
                              item => (item === index || animationTimer > 1) ? itemColorLetter = ( event ? {color:'white'} :  {color:'grey'}) : itemColorLetter
                            );
                              return itemColorLetter;
                      })
                    )
                }

                
            function timeOutOfAnimation() {
                setTimeout(() => {
                OneTimeOutOfAnimation();
                  animationTimer += 1;
                    if(animationTimer > 2) return 0;
                timeOutOfAnimation();
                },80 
              );
            }
        timeOutOfAnimation();
      }

      
    return (

      <div className='Flex Navbar uprcass'>
         
          <CreateLink hendlerColorOverOut = {hendlerColorOverOut}  arrPages = {arrPages}/>
      </div>
    )
}


