export default function(state, action){
    let newstate = state
    switch(action.type){
        case 'delete': return state.filter(it => {
            return it && !(it.id === action.id)
        })
        case 'add_remov': return state.map(item => {
            if(item.id === action.e.id) item.userId = item.userId + (1*action.e.plusmin)
            if (item.userId === 0) item.userId = 1
            return item
        })
        case 'check_or_add_product_in_basket': return(function(){

             let flagpresentprod = false;
 
                state.filter(item => {
                    return item.id === action.product.id ? flagpresentprod = !flagpresentprod  : item;
                })
                if(!flagpresentprod){
                    action.product.userId = action.counterProduct;
                    return state = state.concat(action.product)
                } 
                 console.dir(state);
                return state
            }()
        )
        case 'init_products_from_local_storage': return state = action.prd
        default: return newstate;
    }
}