import React, { useState ,useEffect, useReducer} from "react";
import Basket from './Basket'
import logo from './images/image.png';
import basket from './images/basket.png';
import search from './images/Group 23.png';
import img1 from './images/01.png';
import img2 from './images/02.png';
import img3 from './images/03.png';
import butbold from './images/butbold.png';
import './styles/App.scss';
import Reducer from './Reducer';



let sliderimg = [img1,img2,img3];


function Viking(props){
      
    let [products, dispatch] = useReducer(Reducer, [])
    
    let [onenbergernav, setOnenbergernav ] = useState(true);
    let [openBas, setOpenBas] = useState(false);
    let [idProduct, setIdProduct] = useState(0);
    let [counterProduct, setCounterProduct] = useState(1)
    let [quantity, setQuantity] = useState(0)


    useEffect(() => {
        props.openPage(false);
      }, []);

    useEffect(() => {
        dispatch({type: 'init_products_from_local_storage', prd: JSON.parse(localStorage.getItem('products'))})  
    }, []);


    useEffect(() => {
        quantity_products_in_basket();
        localStorage.setItem('products', JSON.stringify(products))},[products])
    


    function quantity_products_in_basket(){
        setQuantity(products.reduce(function(sum, current) {
            return sum + current.userId;
          }, 0))
    }

    function hendler_open_close_basket(){setOpenBas(!openBas)}

    function hendler_delete_product(id){
        dispatch({type: 'delete', id:id})
        quantity_products_in_basket()
    }

    function add_remov_prod_basket(e){
        dispatch({type: 'add_remov', e: e})
        quantity_products_in_basket()
    }

    function hendlerslide(id){
        idProduct = (idProduct + (id*1))%3
            if(idProduct === -1) idProduct = sliderimg.length - 1;
            setIdProduct(idProduct)
    }


    function addremove_quantity_products(plusminus){
        setCounterProduct(counterProduct + (plusminus*1) < 1 ? counterProduct = 1 : counterProduct = counterProduct + (plusminus*1))
    }

    function hendlerGetDataFromServer(){
        fetch(`https://jsonplaceholder.typicode.com/todos/${idProduct+1}`)
            .then((response) => {
                return response.json();
            }).then((product) => {
                dispatch(
                    {type: 'check_or_add_product_in_basket', product: product, counterProduct: counterProduct})
                    quantity_products_in_basket()
            });
    }


 
  return (
      <div>
      <div className = 'Viking__wrapper'>

         {openBas ? <Basket 
            hendler_open_close_basket = {hendler_open_close_basket}
            add_remov_prod_basket = {(e) => add_remov_prod_basket(e) }
            hendler_delete_product = {(id) => hendler_delete_product(id)}
            products = {products}
            /> : null} 
          <nav className="Viking__navbar">
              <div className="Viking__container">
                  <div className="Viking__navbar__line">
                      <div 
                        onClick = {() => {setOnenbergernav(!onenbergernav)}}
                        className="Viking__burgerMenu">
                          <div className = 'Viking__burgerMenu__line'></div>
                          <div className = 'Viking__burgerMenu__line Viking__short'></div>
                          <div className = 'Viking__burgerMenu__line'></div>
                          
                      </div>
                      <div className="Viking__logo"><img src={logo} alt=""/></div>
                      <div className="Viking__navbar__items">
                          <ul className = 'Viking__navbar__navlinks'>
                         { onenbergernav ?  <ul className = 'Viking__navbar__navlinkssrc'>
                                <li className="Viking__navbar__link">Vikings</li>
                                <li className="Viking__navbar__link Viking__width2">Ancient Egypt</li>
                                <li className="Viking__navbar__link">Celtics</li>
                                <li className="Viking__navbar__link">Sale</li>
                                <li className='Viking__navbar__exitnavberger'>
                                    <button  className='Viking__navbar__exitnavberger'
                                     onClick = {() => {setOnenbergernav(!onenbergernav)}}>EXIT</button>
                                </li>
                              </ul>: null}                              
                              <li className= 'Viking__navbar__serch'><img src={search} alt=""/></li>
                              <li className="Viking__navbar__linkbasket" 
                              onClick = {hendler_open_close_basket}><img src={basket} alt=""/>
                                    <div className = 'Viking__navbar__redCircus'>
                                        {quantity}
                                    </div>
                              </li>
                          </ul> 
                        
                      </div>
                  </div>   
              </div>
          </nav>
          <div className="Viking__content">
              <div className="Viking__container">
                  <div className="Viking__content__line">
                      <div className="Viking__content__images">
                          <img className="Viking__content__bigimg" src={sliderimg[idProduct]} alt=""/>
                          <div className = 'Viking__content__ltimages'>
                                <button  onClick = {() => hendlerslide(-1)} className='Viking__button-inout'>
                                    <img className = 'Viking__button-rotate' src={butbold} alt=""/>
                                </button>
                                <div>
                                    <img className="Viking__content__litleimg"
                                         onClick = {() => setIdProduct(0)} src={img1} alt=""/>
                                 <img className="Viking__content__litleimg"
                                         onClick = {() => setIdProduct(1)} src={img2} alt=""/>
                                    <img className="Viking__content__litleimg" 
                                        onClick = {() => setIdProduct(2)} src={img3} alt=""/>
                                </div>
                                <button onClick = {() => hendlerslide(1)} className='Viking__button-inout'>
                                    <img src={butbold} alt=""/> 
                                </button>
                          </div>
                         
                      </div>
                      <div className="Viking__content__information">
                          <div></div>
                          <div className="Viking__content__mainTitle">
                              <span>
                                  Handmade Stainless Steel Kings Chain Viking Geri And Freki Mjolnir Necklace
                              </span>
                            </div>
                          <div className="Viking__content__choseTitle">Choose your length</div>
                          <div className="Viking__form">
                              <select className='Viking__form__input' type="text" >
                                  <option>60 CM (24 INCHES)</option>
                                  <option>60 CM (24 INCHES)</option>
                                  <option>60 CM (24 INCHES)</option>
                                  <option>60 CM (24 INCHES)</option>
                              </select>
                            
                          </div>
                          <div className="Viking__content__texInc">
                              <span className = 'Viking__content__price'>$19.95</span>
                              <span className = 'Viking__content__Taxincluded'> Tax included</span>
                          </div>
                          <div className="Viking__button">
                              <button 
                                onClick = {() => addremove_quantity_products(-1)} 
                                className="Viking__button-plusminus">-</button>
                              <button className="Viking__button-number">{counterProduct}</button>
                              <button 
                                 onClick = {() => addremove_quantity_products(+1)} 
                                className="Viking__button-plusminus">+</button>
                              <button onClick = {hendlerGetDataFromServer} className="Viking__button-red">Add To Cart</button>
                          </div>
                          <div className="Viking__content__text">
                                <p className="Viking__content--bold">Handmade Chain Geri and Freki Viking Necklace with Thor Hammer Mjolnir!</p>
                                <p>The Kings chain was worn by Viking kings as a status symbol as the name applies. This is a 100% handmade Stainless Steel Chain. The crafting is a very time consuming process to put together the chain link by link.</p>
                                <p> The hand made chain connects together with a lobster clamp.</p>
                          </div>
                      </div>
                      
                  </div>
              </div>
          </div>
      </div>
    </div>  
  );

}

export default Viking;