import React, {useState} from "react";
import Navbar from './Navbar';
import EveKeyControl from './Items3D/EveKeyControl';
import {HashRouter, Route, Redirect,NavLink} from 'react-router-dom';
import Home from './Home';
import Portfolio from './Portfolio';
import Resume from './Resume';
import Contacts from './Contacts';
import Item3D from './Items3D/Item3D';
import useEventListener from 'react-use-event-listener'


function App() {

  let[menuPosition, setMenuPosition] = useState('0.00:0.00:0.00')

  let[move, setMove] = useState(false);
  let[deepMove, setDeepMove] = useState(false);
  let[keyRotate, setKeyRotate] = useState(false);
  let[keyBackCol, setKeyBackCol] = useState(false);
  let[showElements, setShowElements] = useState(true);
  let[opacity, setOpacite] = useState(true);
  let[restart, setRestart] = useState(false);



useEventListener('keydown',e => {
    if(e.code === 'KeyW' || e.code === 'KeyS') setMove(e.code)
    if(e.code === 'ArrowLeft' || e.code === 'ArrowRight') setKeyRotate(e.code)
    if(e.code === 'KeyA' || e.code === 'KeyD') setKeyBackCol(e.code)
    if(e.code === 'ArrowUp' || e.code === 'ArrowDown') setDeepMove(e.code)
});


useEventListener('keyup',e => {
   if(e.code === 'KeyW' || e.code === 'KeyS')  setMove(false)
   if(e.code === 'ArrowLeft' || e.code === 'ArrowRight') setKeyRotate(false)
   if(e.code === 'KeyA' || e.code === 'KeyD') setKeyBackCol(false)
   if(e.code === 'ArrowUp' || e.code === 'ArrowDown') setDeepMove(false)
});

function numberMenu(x,y,z) {
  setMenuPosition(`${(x - 11.5).toFixed(2)}:${(y + 5.5).toFixed(2)}:${(z - 985).toFixed(2)}`)
}


function openPage(e) {
  setShowElements(e)
}


function opacitytext() {
  setOpacite(opacity = !opacity)
}

function restarteveposition(e) {
  setRestart(e)
}

  return (
    <HashRouter basename={process.env.PUBLIC_URL}>   
      <div className = 'App'>
     { showElements ?<Item3D
            restart = {restart}
            move = {move}
            deepMove = {deepMove}
            keyBackCol = {keyBackCol}
            keyRotate = {keyRotate}
            numberMenu = {(x,y,z) => numberMenu(x,y,z)}
          /> : null }
              <div className = 'App__Items'>
                <div className = 'Container'
                  style = {!showElements ? {padding: '0px -30px'} : null }
                >
                  { showElements ? <Navbar/> : null}
                    <div className = 'App__PageContent'>
                 <Route path = '/Home'>
                  {    opacity ? <Home /> : null }
                 </Route>
                    <Route path = '/Portfolio' >
                   {    opacity ?    <Portfolio showElements = {showElements} openPage = {(e) => openPage(e)}></Portfolio> :  null}
                   </Route> 
                    <Route path = '/Resume' >
                      {    opacity ? <Resume /> : null }
                    </Route>
                    
                    <Route path = '/Contacts' >
                    {    opacity ? <Contacts /> : null }
                    </Route>
                    {/* <Redirect from='/' to='/Home'/> */}
                        
                    </div>
                      { showElements ? <EveKeyControl 
                          menuPosition = {menuPosition}
                          keyBackCol = {keyBackCol}
                          deepMove = {deepMove}
                          move = {move} 
                          keyRotate = {keyRotate}
                          restarteveposition = {e => restarteveposition(e)}
                          opacitytext = {e => opacitytext(e)}
                      /> : null}
                </div>
            </div>
        </div>  
    </HashRouter>
  );
}

export default App;
