import React, { useEffect, useState } from 'react';

let textp1 = 'I am a web developer with 1.5 years of experience. In 2019, i graduated from the KPI Faculty of Information Security. During my studies, I actively studied C ++, JS and AHDL.'
let textp2 = 'After that I worked in the structure of special communications, and now I teach courses on programming for applicants. Mainly I develop single-page applications on ReactJS framework using basic ThreeJS.'
let textp3 = 'If you have a job offer, project proposal, or just want to say hello, email me at vladosik4891@gmail.com.'

export default function Resume() {
  let[restext1, setResText1] = useState('');
  let[restext2, setResText2] = useState('');
  let[restext3, setResText3] = useState('');
  
     useEffect(() => {
      
     

       function addletertotext(res, tx, setTx, numtext) {
         setTimeout(() => {
           
          setTx(tx += `${numtext[tx.length]}`)  
          
          if(tx.length === numtext.length) return res !== 0 ? res() : 0;
           addletertotext(res, tx, setTx, numtext)
         }, 10);

     }

     new Promise((resolve, reject) => {
        addletertotext(resolve, restext1, setResText1, textp1 )
     }).then(() => {
       return new Promise((resolve, reject) => {
        addletertotext(resolve, restext2, setResText2, textp2 )
       })
     }).then(() => {
         addletertotext(0, restext3, setResText3, textp3 )
      })
     }
    
    ,[])

  return (
      <div className = 'Resume Style__Back cWhit'>
        <div className = 'Container'>
          <div className = 'Home__Resume Flex JsCr AlgCr '>
            <p className = 'wdt65 Resume__width'>
              <p className = 'resume__padd resume__paddtab'>{restext1}</p>
              <p className = 'resume__padd resume__paddtab'>{restext2}</p>
              <p className = 'resume__padd resume__paddtab'>{restext3}</p>
              
            </p>    
          </div>
        </div>
      </div>
  )}



