import React, {useEffect, useState} from "react";
import './howerstyle.css';
import logo from "./images/logo.png";
import colorHover from "./images/colorHover.png";
import colorLine from "./images/colorLine.png";
import designHover from "./images/designHover.png";
import forWhom from "./images/forWhom.png";

import linePlay from "./images/linePlay.png";
import modeHover from "./images/modeHover.png";


import playButton from "./images/playButton.png";
import rizorHovertrack from "./images/rizorHovertrack.png";
import saw from "./images/saw.png";
import sliderImg from "./images/sliderImg.png";

import { Element, scroller } from 'react-scroll'




let hovnavbar = [
  {display: 'none'},
  {display: 'none'},
  {display: 'none'}
]

let hovcolorline = [
 
  {display: 'none'},
  {display: 'none'},
  {display: 'none'},
  {display: 'none'},
  {position:'absolute', top: '30px', left: '6px', width:'5px', height:'50px', backgroundImage:`url(${sliderImg})`},
]

export default function HowerTrax(props){

  let[hovlinkstyle, setHovlinkstyle] = useState(hovnavbar)
  let[hovline, setHovline] = useState(hovcolorline)

      function hovlinkanim(el, overout) {
        setHovlinkstyle(hovlinkstyle.map(
          (it, ind) => {
            return(
              overout && el === ind ?  {position:'absolute', bottom: '-3px', left: '-4px', width: '15px', height:'1px', background:'#0300ff'} :  {display: 'none'} 
              )
          }
        ))
      }


      function hovcoloranim(el) {
        console.log('el',el);
        setHovline(hovline.map(
          (it, ind) => {
            return(
               el === ind ? {position:'absolute', top: '30px', left: '6px', width:'5px', height:'50px', backgroundImage:`url(${sliderImg})`} :  {display: 'none'} 
              )
          }
        ))
      }

      function scrollTo(toscrolllink){
        scroller.scrollTo(`scroll-to-element__${toscrolllink}`, {
          duration: 800,
          delay: 0,
          smooth: 'easeInOutQuart'
        });
      }



      useEffect(() => {
      
        props.openPage(false);
      }, []);


 
      return (
        <div>
         <div className="howertrax__wrap">
         <Element name="scroll-to-element__howertrax__headId" />
            <div className="howertrax__heder">
              <div className="howertrax__container">
                <div className="howertrax__heder__body">
                  <div className="howertrax__heder__logo">
                    <img src={logo} alt="howertrax__logo" />
                  </div>
                  <div className="howertrax__heder__nav">
                    <div className="howertrax__heder__link"
                      onMouseEnter = {() => hovlinkanim(0, true)}
                       onMouseLeave = {() => hovlinkanim(0, false)}
                    >
                      <div style = {hovlinkstyle[0]}></div>                    
                      <a onClick={() => scrollTo('howertrax__advantageId')}>ПРЕЙМУЩЕСТВА</a></div>
                     <div className="howertrax__heder__link"
                        onMouseEnter = {() => hovlinkanim(1, true)}
                        onMouseLeave = {() => hovlinkanim(1, false)}
                    >
                      <div style = {hovlinkstyle[1]}></div>
                      <a  onClick={() => scrollTo('howertrax__modeId')}>ХАРАКТЕРИСТИКИ</a></div>
                    <div className="howertrax__heder__link"
                      onMouseEnter = {() => hovlinkanim(2, true)}
                      onMouseLeave = {() => hovlinkanim(2, false)}    
                    >
                      <div style = {hovlinkstyle[2]}></div>
                      <a onClick={() => scrollTo('howertrax__forwhomId')}>ДЛЯ КОГО</a></div>
                  </div>
                </div>
              </div>    
            </div>
            <div className="howertrax__content">
              <div className="howertrax__rizor" >
                <div className="howertrax__container">
                  <div className="howertrax__rizor__body">
                    <div className="howertrax__rizor__row howertrax__max__width">
                      <div className="howertrax__rizor__saw">
                        <img src={saw} alt="" />
                      </div>
                      <div className="howertrax__rizor__hover">
                        <img src={rizorHovertrack} alt="" />
                      </div>
                    </div>
                    <div className="howertrax__rizor__title">
                      <div className="howertrax__rizor__titleVideo">
                        <p className="howertrax__rizor__bigsizetext">RIZOR</p> 
                        <div className="howertrax__rizor__video">
                          <div className="howertrax__rizor__linePlay">
                            <p className="howertrax__rizor__textPlay">СМОТРЕТЬ <br /> ВИДЕО</p>
                            <div className="howertrax__rizor__imgLine">
                              <img src={linePlay} alt="" />
                            </div>       
                          </div>
                          <div className="howertrax__rizor__button">
                            <img src={playButton} alt="" />
                          </div>
                        </div>
                      </div>
                      <p className="howertrax__rizor__bigsizetext"> HOVERTRAX <br />4.0 </p> <br />
                      <p className="howertrax__rizor__littlesizetext">МАНЕВРЕНОСТЬ, ЛЕГКОСТЬ,<br />ЭКОЛОГИЧНОСТЬ</p>
                    </div>
                  </div>
                </div>
              </div>
              <Element name="scroll-to-element__howertrax__advantageId" />
              <div className="howertrax__advantage" >
                <div className="howertrax__container">
                  <div className="howertrax__advantage_body">
                    <div className="howertrax__advantage__background howertrax__max__width">
                      <div className="howertrax__advantage__video">
                        <img src={designHover} alt="" />
                      </div>
                      <div className="howertrax__advantage_bigTitle">
                        <p>4.0 <br /> RIZOR</p>
                      </div>
                    </div>
                    <div className="howertrax__advantage__row">
                      <div className="howertrax__advantage__design">
                        <p className="howertrax__advantage__mainTitle">ПРЕЙМУЩЕСТВА</p>
                        <p className="howertrax__advantage__title howertrax__general__title">ДИЗАЙН</p>
                        <p className="howertrax__advantage__text howertrax__general__text">Внешний вид дышит <br /> идеями футуризма:</p>
                        <ul className="howertrax__advantage__designList howertrax__general__list">
                          <li> плавные линии</li>
                          <li> подсветка светодиодными <br /> индикаторами </li>
                          <li> рельевные бамперы,<br /> оснащкнные защитными панелями</li>
                        </ul>
                      </div>
                      <div className="howertrax__advantage__color">
                        <div className="howertrax__advantage__colorRow">  
                          <div className="howertrax__advantage__colorText">
                            <div className="howertrax__advantage__colorLine">
                              <img src={colorLine} alt="" />
                            </div>
                            <div className="howertrax__advantage__colorColumn">
                              <p className="howertrax__advantage__title howertrax__general__title"> ЦВЕТ</p> 
                              <p className="howertrax__advantage__text howertrax__general__text"> Модель представлена сразу <br /> в нескольких цветах</p>
                              <div className="howertrax__advantage__colorList">
                                <div id="howertrax__advantage__colorGreen" className="howertrax__advantage__colorItem" 
                                onClick = {() => hovcoloranim(0)}><div  style = {hovline[0]}></div> </div>
                                <div id="howertrax__advantage__colorBlue" className="howertrax__advantage__colorItem" 
                                onClick = {() => hovcoloranim(1)}><div  style = {hovline[1]}></div></div>
                                <div id="howertrax__advantage__colorRed" className="howertrax__advantage__colorItem" 
                                onClick = {() => hovcoloranim(2)}><div  style = {hovline[2]}></div></div>
                                <div id="howertrax__advantage__colorGrey" className="howertrax__advantage__colorItem" 
                                onClick = {() => hovcoloranim(3)}><div  style = {hovline[3]}></div></div>
                                <div id="howertrax__advantage__colorBlack" className="howertrax__advantage__colorItem" 
                                onClick = {() => hovcoloranim(4)} ><div  style = {hovline[4]}></div></div>
                              </div>
                            </div>  
                            <div className="howertrax__advantage__colorImage">
                              <img src={colorHover} alt="" />
                            </div>
                          </div>
                        </div>     
                      </div>
                    </div> 
                  </div>
                </div>
              </div>
              <Element name="scroll-to-element__howertrax__modeId" />
              <div className="howertrax__mode" id="howertrax__modeId">
                <div className="howertrax__container">
                  <div className="howertrax__mode__body ">
                    <div className="howertrax__mode__row">
                      <div className="howertrax__mode__video">
                        <img src={modeHover} alt="" />
                      </div>
                      <div className="howertrax__mode__column">
                        <div className="howertrax__mode__titleLine">
                          <p className="howertrax__mode__title howertrax__general__title">РЕЖИМЫ</p> 
                          <div className="howertrax__mode__gorizontLine">
                            <div className="howertrax__mode__itemLine" />
                          </div>
                        </div>
                        <p className="howertrax__mode__text howertrax__general__text">Помимо «умной» подсветки, Hovertrax 4.0 <br /> снабжён двумя скоростными режимами</p>
                        <ul className="howertrax__mode__list howertrax__general__list">
                          <li>Один для продвинутых пользователей</li>
                          <li>Второй для новичков</li>
                        </ul>
                        <div className="howertrax__mode__rowItem">
                          <div className="howertrax__mode__speed">
                            <p className="howertrax__mode__number">13км/ч</p>
                            <p className="howertrax__mode__speedText">Максимальная <br /> скорость</p>
                          </div>
                          <div className="howertrax__mode__quantit">
                            <p className="howertrax__mode__number">2</p>
                            <p className="howertrax__mode__speedText">Скоростных <br /> режима</p>
                          </div>
                        </div>      
                      </div>
                      <div className="howertrax__mode__lineColumn">
                        <div className="howertrax__mode__verticalLine">
                          <div className="howertrax__mode__circusLine" />
                        </div>
                        <div className="howertrax__mode__buttonText">              
                          <p className="howertrax__mode__textVideo"> СМОТРЕТЬ <br /> ВИДЕО</p>
                          <div className="howertrax__mode__buttonPlay">
                            <img src={playButton}alt="" />
                          </div>                                          
                        </div>  
                      </div>
                    </div>      
                  </div>
                </div>
              </div>
            </div>
            <Element name="scroll-to-element__howertrax__forwhomId" />
            <div className="howertrax__forwhom zind8" id="howertrax__forwhomId">
              <div className="howertrax__container">
                <div className="howertrax__forwhom__body">
                  <div className="howertrax__forWhome__row howertrax__max__width">
                    <div className="howertrax__forwhom__saw">
                      <img src={saw} alt="" />
                    </div>
                    <div className="howertrax__forwhom__img">
                      <img src={forWhom} alt="" />
                    </div>
                  </div>
                  <div className="howertrax__forwhom__text">
                    <p className="howertrax__forwom__title">ДЛЯ КОГО</p>
                    <p className="howertrax__forwhom__inform">При максимально выдерживаеой нагрузки в 100 кг отлично подойдёт, как для подростающего поколения, так и дляболее взрослых "наездников"</p>
                    <p className="howertrax__forwhom__inform">Это единственный гироскутер со специальным детским режимом езды</p>
                    <p />
                    
                    <div className="howertrax__forwhom__button">
                    
                      <button className="howertrax__forwhom__buttonClick">ПЕРЕЙТИ В КАТАЛОГ</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="howertrax__footer">
              <p> Made by Vladislav Chornenkyi</p>
            </div>
          </div>
        
        </div>
      );
    }
  

 