import React, {Component} from "react";
import * as THREE from "three";
import earth from "./PagesAll__images/earthcolor.png";
import EVE from "./eve-from-wall-e/0.obj";
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader.js';
import faceEve from './eve-from-wall-e/face.png';



class Item3D extends Component{
  constructor(props){
    super(props)
 //   this.state = this.props;
    this.eve = false;
    this.angleX = 90;
    this.angleY = 0;
    this.angleRotate = 0;
    this.mashes = [];
  }
 
    componentDidMount(){
      // console.log(this.props);

    //  console.log(this.state);
      const width = this.mount.clientWidth
      const height = this.mount.clientHeight
      //ADD SCENE
      this.scene = new THREE.Scene()
      //ADD CAMERA
      this.camera = new THREE.PerspectiveCamera(60, window.innerWidth/window.innerHeight, 1, 10000)
      this.camera.position.z = 1000;
      //ADD RENDERER
      this.renderer = new THREE.WebGLRenderer({ antialias: true })
      this.renderer.setClearColor('#000000')
      this.renderer.setSize(width, height)
      this.mount.appendChild(this.renderer.domElement)
      //ADD SPHERE
      let textureSphere = new THREE.TextureLoader().load( earth );
      let geometrySphere = new THREE.SphereGeometry(400, 25, 25);
      let sphereMaterial = new THREE.MeshBasicMaterial({map:textureSphere})  
      this.sphere = new THREE.Mesh(geometrySphere, sphereMaterial)
      //ADD LIGHT
        let ambient = new THREE.AmbientLight( 0x101030 );
        this.scene.add( ambient );
        let directionalLight = new THREE.DirectionalLight( 0xffeedd );
        directionalLight.position.set( 0, 0, 1 );
        this.scene.add( directionalLight );
        //ADD GROUP STARS
        this.group = new THREE.Object3D();
          for(let i = 0; i < 100; i++){
          let geometryStars = new THREE.SphereGeometry(2);
          let starsMaterial = new THREE.MeshBasicMaterial({color: 0xffffff})
              let stars = new THREE.Mesh(geometryStars, starsMaterial)

              function positionStars(starDistance){
                  return(170 + 650*Math.random()) *  (Math.random() > 0.5 ? -1: 1);  
              }
                  stars.position.x = positionStars()
                  stars.position.y = positionStars()
                  stars.position.z = positionStars()
                  this.group.add(stars);
          }
        //ADD EVE
        let loadEveObject = new Promise((resolve, reject) => {
            this.textureFace = new THREE.TextureLoader().load(faceEve);      
            this.loader = new OBJLoader();
     
            this.loader.load( EVE , object => {
           //   console.log(object);
              
            //console.log(object);
              object.traverse( child => {if( child instanceof THREE.Mesh) this.mashes.push(child)});
              this.mashes[0].material = new THREE.MeshPhongMaterial({map:this.textureFace});

               object.position.z = +985
               object.position.y = -5.5
               object.position.x = 11.5

      
            resolve(object);
           } );
        });
        
          loadEveObject.then((successMessage) => {
            this.eve = successMessage
            this.scene.add(successMessage);
          }); 
                 
              
       
      // ADD STARS & EARTH on SCENE
   
      this.sphere.rotateZ(Math.PI )
      this.scene.add(this.sphere)
      
      this.scene.add(this.group)
      this.start();
        
    }
    componentWillUnmount(){
        this.stop()
        this.mount.removeChild(this.renderer.domElement)
      }
    start = () => {
        if (!this.frameId) {
          this.frameId = requestAnimationFrame(this.animate)
        }
      }
    stop = () => {
        cancelAnimationFrame(this.frameId)
    }

   animate = () => {

      let BeginMoveEve = (speed) => {
        if(this.angleX < 0) this.angleX = 360
        if(this.angleY <  0) this.angleY = 360
        if(this.angleX > 360) this.angleX = 0
        if(this.angleY > 360) this.angleY = 0
       
          this.eve.position.y += speed *(Math.cos(THREE.Math.degToRad(this.angleY)))
          this.eve.position.x += speed *(Math.cos(THREE.Math.degToRad(this.angleX)))  
      }

      let choseSideMove = (direct) => {
         this.angleY += direct*0.8
         this.angleX += direct*0.8
      }

      let deepMove = (direct) => {
        console.log(this.eve.position.z);
        if(this.eve.position.z >= 970 && this.eve.position.z <= 990 ){
          this.eve.position.z += direct*0.03
        }
        else{
          if(this.eve.position.z < 975) this.eve.position.z = 970
          if(this.eve.position.z > 990) this.eve.position.z = 990
        }
      }


      let eveRotate = (direct) => {
         
            //  this.eve.traverse( child => {if( child instanceof THREE.Mesh) this.mashes.push(child)});
             this.mashes[2].position.y = +0.7
                for(let i = 0; i < this.mashes.length; i++){
                  this.mashes[i].rotation.y += direct*0.08
                }
      }


      if(this.eve) {


          if(this.props.keyRotate){
              if(this.props.keyRotate === 'ArrowLeft')  eveRotate(-1)
              if(this.props.keyRotate === 'ArrowRight') eveRotate(1)
          }
          else{
            // let meshes = [];
            //   this.eve.traverse( child => {if( child instanceof THREE.Mesh) meshes.push(child)});
              this.mashes[2].position.y = 0
          }
            

          if(this.props.move){
          this.scene.remove(this.evebacklight)
            if(this.props.move === 'KeyW'){
              BeginMoveEve(0.09)
                if(this.props.keyBackCol === 'KeyD') choseSideMove(-1)
                if(this.props.keyBackCol === 'KeyA') choseSideMove(1)
              
            }
            else{
              BeginMoveEve(-0.03) 
                if(this.props.keyBackCol === 'KeyD') choseSideMove(-1)
                if(this.props.keyBackCol === 'KeyA') choseSideMove(1)
            }
          }
            
          if(this.props.deepMove){
            if(this.props.deepMove === 'ArrowUp') deepMove(-1)
            if(this.props.deepMove === 'ArrowDown') deepMove(1) 
          }
          


             
            
        if(!this.props.move){
         
            if(this.props.keyBackCol === 'KeyD'){
                choseSideMove(-1)
                BeginMoveEve(0.03)                
            }
            if(this.props.keyBackCol === 'KeyA'){
                choseSideMove(1)
                BeginMoveEve(0.03)  
            }         
        }

        if(this.props.restart){
          this.eve.position.z = +985
          this.eve.position.y = -5.5
          this.eve.position.x = 11.5
          this.angleX = 90;
          this.angleY = 0;
          for(let i = 0; i < this.mashes.length; i++){
            this.mashes[i].rotation.y = 0
          }
        }
          
          this.eve.rotation.z = THREE.Math.degToRad(this.angleY);
          this.props.numberMenu(this.eve.position.x, this.eve.position.y, this.eve.position.z)
      }
      
      
      this.sphere.rotation.y += 0.0009;
      this.group.rotation.y += 0.00009;
      this.renderScene()
      this.frameId = window.requestAnimationFrame(this.animate)
    }
    renderScene = () => {
      this.renderer.render(this.scene, this.camera)
    }
    render(){
      return(
        <div
          style={{ width: window.innerWidth, height: window.innerHeight }}
          ref={(mount) => { this.mount = mount }}
        />
      )
    }
}

export default Item3D