import React, {useState} from 'react';
import {NavLink} from 'react-router-dom';
import {HashRouter, Route} from 'react-router-dom';
import Hower from './howertrax/HowerTrax';
import Pirol from './pirolsite/Pirol';
import Viking from './vikingsite/Viking';
import GameofLife from './gameoflifesite/GameofLife';
import useEventListener from 'react-use-event-listener'


export default function Portfolio(props) {

let[padd, setPadd] = useState(0);


  useEventListener('mousemove',e => {
    setPadd(((window.screen.width/2) - e.clientX) * 0.02)
  });

  return (
      <div className = 'Portfolio Style__Back'>
          <div className = 'Container'>
          {props.showElements ? <h1>Portfolio</h1> : null}
          <HashRouter basename={process.env.PUBLIC_URL}>
            <div className = 'Flex dispsp' style = {padd >= 0 ? {paddingLeft:`${padd}px`} : {paddingRight:`${(-1)*padd}px`}}>
              {props.showElements ? <NavLink target="_blank" to = {'/Portfolio/howertrax/HowerTrax'}>
                  <div className = 'portfilio__howback portfolio__link'></div>
              </NavLink> : null }
              {props.showElements ? <NavLink target="_blank" to = {'/Portfolio/pirolsite/Pirol'}>
                <div className = 'portfilio__pirol portfolio__link'></div>
              </NavLink> : null } 
                 {props.showElements ? <NavLink target="_blank" to = {'/Portfolio/gameoflifesite/GameofLife'}>
                <div className = 'portfilio__gameoglife portfolio__link'></div>
              </NavLink> : null }
              {props.showElements ? <NavLink target="_blank" to = {'/Portfolio/vikingsite/Viking'}>
                <div className = ' portfilio__barber portfolio__link'></div>
              </NavLink> : null }
            </div>
              <div>
                  <Route path = '/Portfolio/howertrax/HowerTrax'>
                      <Hower openPage = {(e) => {props.openPage(e)}} />
                  </Route>
                  <Route path = '/Portfolio/pirolsite/Pirol'>
                      <Pirol openPage = {(e) => {props.openPage(e)}}/>
                  </Route>
                  <Route path = '/Portfolio/gameoflifesite/GameofLife'>
                      <GameofLife openPage = {(e) => {props.openPage(e)}} />
                  </Route>
                  <Route path = '/Portfolio/vikingsite/Viking'>
                      <Viking openPage = {(e) => {props.openPage(e)}}/>
                  </Route>
              </div>
            </HashRouter>
          </div>
        </div>
  )}