import React, { useState } from 'react';

let stylebutton = { width: '90px', height: '25px', background: 'black', borderRadius: '5px', color: 'white'}

let chacgeModeSize = false;

export default function FuncChangeSize(props){

    let[width, setWidth] = useState(30);
    let[height, setHeight] = useState(20);
    let[button, setButton] = useState(stylebutton);


    return(
        <div className = 'gameoflife__App__changeSize'>
            <div className = 'gameoflife__App__buttonChange'>
                <button style = {button} 
                    onClick = {
                        () => {props.ChangeSize( height, width);
                        chacgeModeSize = !chacgeModeSize;
                        chacgeModeSize ? setButton({width: '90px', height: '25px', background: 'aqua', borderRadius: '5px', color: 'black',} ) : setButton(stylebutton);}}>
                            {chacgeModeSize ? 'Apply settings' : 'Change size' }
                </button> 
            </div>    
            <h1 className ='gameoflife__App__h1'>Width 
                <input className ='gameoflife__App__input'
                    value = {width}
                    type = 'number'
                    min="0"
                    disabled = {!chacgeModeSize}
                    onChange = {(e) => {e.target.value >= 0 && e.target.value <= 60 ? setWidth( e.target.value) :  setWidth(60)}}>
                </input>
            </h1>
            <h1 className ='gameoflife__App__h1'>Height
                <input className ='gameoflife__App__input'
                    value = {height}
                    type = 'number'
                    min="0"            
                disabled = {!chacgeModeSize}
                    onChange = {(e) => {e.target.value >= 0 && e.target.value <= 40 ? setHeight( e.target.value) : setHeight( 40)}}>
                </input>
            </h1>
        </div>
        
    )
}