import React, { useState, useEffect } from 'react';
import ButtonForm from './ButtonForm';
import InputForm from './InputForm';
import FuncChangeSize from './FuncChangeSize';
import ItemsField from "./ItemsField";
import './gameoflifestyle.css'




let colorBLACK =  {width: '25px', height: '25px',background: 'black',  border: '1px solid grey'};
let colorAQUA = {width: '25px',height: '25px', background: 'aqua',  border: '1px solid grey'};
let initState = true;
let startARRAY = [];
let globalTIMER;
let intervalIDENTIFY;
  
    function buildStartField( fieldheight, fieldwidth){
        for(let i = 0; i < fieldheight; i++){
          startARRAY[i] = [];
          for(let j = 0; j < fieldwidth; j++){
            if(initState && ((j === 14 && (i ===6 || i === 7 || i === 8 || i === 10 || i === 11 || i ===12 ))
              || (i === 9 && ( j === 10 || j === 11 || j === 12 || j === 16 || j === 17 || j === 18 )))
              )startARRAY[i][j] = colorAQUA;
            else startARRAY[i][j] = colorBLACK;
          }
        }
          initState = false;
        return startARRAY
    }

      buildStartField(20, 30);


export default function GameofLife(props) {


  useEffect(() => {
    props.openPage(false);
  }, []);

  let[fieldwidth, setfieldwidth] = useState(30);
  let[fieldheight, setfieldheight] = useState(20);
  let[sizeAppField, setSizeAppField] = useState({ display: 'flex', flexWrap: 'wrap', outline: '1px solid grey', width: `${25*fieldwidth}px`});
  let [field, setField] = useState(startARRAY); 
  let [globalStateField, setGlobalStateField] = useState('play'); 
  

    function hendlerOneStep(){
                          
     let tempField = [];
      
         for(let i = 0; i < fieldheight; i++ ){
            tempField[i] = [];
            for(let j = 0; j < fieldwidth; j++ ){
              let countNeihborsCell = 0;
              
              let valueNeightborCell = [
                  [i - 1, j - 1],
                  [i - 1, j + 0],
                  [i - 1, j + 1],
                  [i - 0, j - 1],
                  [i + 0, j + 1],
                  [i + 1, j - 1],
                  [i + 1, j - 0],
                  [i + 1, j + 1]];

           
                for(let ivNC = 0; ivNC < valueNeightborCell.length; ivNC++){
                  
                   if(valueNeightborCell[ivNC][0] >= 0 && valueNeightborCell[ivNC][0] < fieldheight
                        && valueNeightborCell[ivNC][1] >= 0 && valueNeightborCell[ivNC][1] < fieldwidth ){ 
                          if (field[valueNeightborCell[ivNC][0]][valueNeightborCell[ivNC][1]].background === 'aqua') countNeihborsCell++;
                      } 
                }

                      
                        if( (field[i][j].background === 'black' && countNeihborsCell === 3) || 
                          (field[i][j].background === 'aqua' && (countNeihborsCell === 3 || countNeihborsCell === 2 )) ) {
                          tempField[i][j] = 'aqua';
                        }
                        else tempField[i][j] = 'black';
                  }
                }
              
                for(let indx = 0; indx < fieldheight; indx++){
                  for(let jndx = 0; jndx < fieldwidth; jndx++){
                      field[indx][jndx] = {width: '25px', height: '25px', background: tempField[indx][jndx], border: '1px solid grey'}            
                  }
                }

            hendlerAddCell();              
      
    }
  
    function hendlerPlay(event){
     
       if(event === 'play'){
         intervalIDENTIFY =  setInterval(() => {  
            hendlerOneStep()}
          , globalTIMER || 100)}
        else clearInterval(intervalIDENTIFY); 
   
    }

    function hendlerClear(){
        
        hendlerPlay('pause');
        setGlobalStateField('play');
        hendlerAddCell( '' , '' , true);
         
    }
 
    function hendlerTimer(e){ 
        
        if( globalStateField !== 'play'){
          globalTIMER = e.target.value;
            hendlerPlay('pause');
              hendlerPlay('play');
        }
        else globalTIMER = e.target.value;  
    }

    function hendlerChangeSize( eventheight, eventwidth){
      
        setfieldwidth(eventwidth);
        setfieldheight(eventheight);
        setSizeAppField({display: 'flex', flexWrap: 'wrap', outline: '1px solid grey', width: `${25*eventwidth}px`});
        startARRAY = [];
        setField(startARRAY);
        setField(buildStartField(eventheight, eventwidth));
       
    }

    function hendlerAddCell(el1, el2, flagClear) {
        let tempArray = []
        
          for(let i = 0; i < fieldheight; i++){
              tempArray[i] = []
                for(let j = 0; j < fieldwidth; j++){
                    tempArray[i][j] = field[i][j];
                    if(flagClear) tempArray[i][j] = colorBLACK;
                }
          }
            if(typeof(el1) === 'number' ) { 
              console.log(el1,el2);
                tempArray[el1][el2].background === 'black' ? tempArray[el1][el2] = colorAQUA : tempArray[el1][el2] = colorBLACK;
          }

        setField(tempArray);
    }

    return (
      <div>
         <div className='gameoflife__Apps'>
        <div className='gameoflife__App__navbar'>
          <div className='gameoflife__App__title'>Game of Life</div>
            <FuncChangeSize ChangeSize = {( height, width) => {hendlerChangeSize( height, width)}}/>
            <InputForm ChangeTime = { (e) => { hendlerTimer(e)}}/>  
            <ButtonForm 
                statePlayPause = {globalStateField}
                ClickPlay = {() => {
                  hendlerPlay(globalStateField);
                  globalStateField === 'pause' ? setGlobalStateField('play') : setGlobalStateField('pause'); 
                }}
                ClickOneStep = {() => {hendlerOneStep()}}
                ClickClear = {() => {hendlerClear()}}/>
        </div>  
        <div className = 'gameoflife__App_content'>
           <div className = 'gameoflife__App__body'>
              <ItemsField sizeAppField = {sizeAppField} 
                  field = {field} 
                  fieldwidth = {fieldwidth}
                  fieldheight = {fieldheight}
                  hendlerAddCell = {(el1, el2) => {hendlerAddCell(el1, el2)}}/>
            </div>
        </div>
       
        <div className = 'gameoflife__App__footer'>
            <p>Made by Vladislav Chornenkyi</p>
        </div>
        
      </div>
      </div>
     
    );
}


