import React from 'react';
import oneStep from './images/oneStep.png';
import play from './images/play.png';
import pause from './images/pause.png';
import clear from './images/clear.png';

let style = {
    img:{
      height: '40px',
      width: '40px'},
    but:{
      margin: '0px 20px 0px 20px'},
  }



export default function ButtonForm(props){

    // let[buttonPlay, setButtonPlay] = useState(['play', play]);
    
    
    return( 
        <div className="gameoflife__butt">       
             <button style = {style.but} onClick = {() => {props.ClickPlay()}}>
                <img src = { props.statePlayPause === 'pause' ?  pause : play }   style = {style.img} alt = 'pause/play' />
            </button>
            <button style = {style.but}>
                <img src = {oneStep} style = {style.img} alt = 'oneStep'  onClick = {() => {props.ClickOneStep()}}/>
            </button>
            <button style = {style.but}>
                <img src = {clear} style = {style.img} alt = 'clear'  onClick = {() => {props.ClickClear()}}/>
            </button>
        </div>
)}