import React from 'react';

 function ItemToBasket(props){
    console.log('props', props);
    return (
        <div className = 'Viking__productbasket'>
            <span>Id: { props.item.id}</span>
            <span>Name: {props.item.title}</span>
            <span>Price: { props.item.userId}</span>
            <span>Discount: {props.item.userId}</span>
            <div>
                <button onClick = {(e) => props.add_remov_prod_basket({id:props.item.id,plusmin:1})}>ADD</button> 
                    Quantity: {props.item.userId}
                <button onClick = {(e) => props.add_remov_prod_basket({id:props.item.id,plusmin:-1})}>REMOVE</button>  
            </div>
            <div>
                <button onClick = { () => props.hendler_delete_product(props.item.id)}>DELETE</button>
            </div>
        </div>
    )
}

export default function Basket(props){
     console.log('props', props);
    return (
        <div className = 'Viking__basket'>
            {props.products.map(item => {
                return <ItemToBasket 
                    key = {props.id}
                    item = {item}
                    add_remov_prod_basket = {(e) => props.add_remov_prod_basket(e)}
                    hendler_delete_product = { id => props.hendler_delete_product(id)}
                    />
            })} 
            <div className = 'Viking__basket__button'>
                <button>BUY</button>
                <button onClick = {props.hendler_open_close_basket}>EXIT</button>
            </div>
        
    </div>
    )
}